* {
  min-height: 0;
  min-width: 0;
}
body {
  background-color: #fff;
  font-family: "Cairo", sans-serif;
}
@media (max-width: 767px) {
  .no-padding-mobile {
    padding: 0;
  }
  .text-center-mobile {
    text-align: center !important;
  }
  .hidden-mobile {
    display: none !important;
  }
}
