.raq-input-container {
  margin-bottom: 20px;
  position: relative;
  margin-top: 40px;
  .raq-classic-input {
    display: block;
    border: none;
    border-bottom: 1px solid #ced4da;
    width: 100%;
    padding: 15px;
    background-color: #FCFCFC;
    &:focus {
      outline: none;
      border-color: $flatBlue;
      background-color: #FBFBFB;
      box-shadow: 0 1px 0 0 $flatBlue;
      + label {
        top: -25px;
        color: $flatBlue !important;
        font-size: 13px;
      }
    }
  }
  .raq-classic-label {
    margin: 0;
    position: absolute;
    top: 15px;
    left: 15px;
    cursor: text;
    transition: all 0.2s ease-in-out;
  }
  &.has-data {
    .raq-classic-input {
      outline: none;
      + label {
        top: -25px;
        font-size: 13px;
        color: #888;
      }
    }
  }
  textarea {
    resize: none;
    min-height: 220px;
  }
}