.settings-box {
  position: fixed;
  left: -250px;
  top: 0;
  background-color: #fdfdfd;
  width: 250px;
  height: 100%;
  z-index: 1000;
  transition: 0.3s ease-in-out;
  border-right: 1px solid #ccc;
  &.opened {
    left: 0;
    transition: 0.3s ease-in-out;
  }
  .gear-box {
    position: absolute;
    right: -37px;
    top: 176px;
    font-size: 26px;
    background-color: #fff;
    padding: 0 7px;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
  }
  .settings-content {
    h3 {
      text-align: center;
      font-size: 18px;
      padding: 10px 0;
      font-weight: bold;
      color: $flatGreen;
    }
    .colors-list {
      padding: 0;
      text-align: center;
      li {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: #eee;
        display: inline-block;
        cursor: pointer;
        &:first-child {
          background-color: $flatGreen;
        }
        &:nth-child(2) {
          background-color: $flatBlue;
        }
        &:nth-child(3) {
          background-color: $flatOrange;
        }
        &:nth-child(4) {
          background-color: $flatBlack;
        }
      }
    }
  }
}
