$themes: (
  green-theme: (
    color: $flatGreen
  ),
  blue-theme: (
    color: $flatBlue
  ),
  orange-theme: (
    color: $flatOrange
  ),
  black-theme: (
    color: $flatBlack
  )
);

@each $theme, $map in $themes {
  .#{$theme} {
    $color: map-get($map, color);
    .settings-box .settings-content h3 {
      color: $color;
    }
    .upper-bar {
      background-color: $color;
    }
    .lower-nav .dropdown-menu {
      background-color: $color;
    }
    .lower-nav .dropdown-menu:before {
      border-color: transparent transparent $color transparent;
    }
    .service-box:hover {
      background-color: $color;
    }
    .news-section .news-box h4 {
      color: $flatGreen;
    }
    .news-section .news-box .overlay {
      background-color: rgba($color, 0.8);
    }
    .news-section .news-box h4 {
      color: $color;
    }
    .news-section .slick-dots li.slick-active button {
      background-color: $color;
    }
  }
}
