.landing-two {
  height: calc(100vh - 224px);
  position: relative;
  overflow: hidden;
  .image-background {
    background-image: url(../imgs/background-01.jpg);
    background-size: cover;
    min-width: 100%;
    min-height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
  }
  .overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: rgba(black, 0.6);
  }
  .event-info {
    position: absolute;
    top: 50%;
    left: 50%;
    color: #fff;
    z-index: 3;
    width: 100%;
    text-align: center;
    transform: translate(-50%, -50%);
    .timer-elements {
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
      @media (max-width: 767px) {
        margin-top: 10px;
        margin-bottom: 10px;
      }
      .the-time {
        font-size: 70px;
        flex: 1;
        max-width: 130px;
        span:last-child {
          display: block;
          font-size: 14px;
        }
        @media (max-width: 767px) {
          font-size: 30px;
          max-width: 60px;
        }
      }
      .separator {
        font-size: 70px;
        margin: 0 20px;
        @media (max-width: 767px) {
          font-size: 20px;
          margin: 0 10px;
        }
      }
    }
    .event-title {
      h2 {
        font-weight: bold;
        font-size: 48px;
        margin: 5px 0 30px;
        @media (max-width: 767px) {
          font-size: 35px;
          letter-spacing: 0;
          margin: 5px 0;
        }
      }
    }
    .event-description {
      font-weight: bold;
      font-size: 28px;
      @media (max-width: 767px) {
        font-size: 16px;
      }
    }
    .event-register {
      button {
        font-weight: bold;
        border: 2px solid #fff;
        color: #fff;
        background: transparent;
        padding: 10px 30px 14px;
        transition: all 0.3s ease-in-out;
        border-radius: 15px;
        font-size: 18px;
        margin-top: 20px;
        @media (max-width: 767px) {
          margin-top: 10px;
          font-size: 16px;
        }
        &:hover {
          background-color: #fff;
          color: $flatBlue;
        }
        &:focus {
          outline: none;
        }
      }
    }
  }
  .scroll-section {
    position: absolute;
    bottom: 30px;
    left: 50%;
    z-index: 3;
    margin-left: -14px;
    transition: all 0.3s ease-in-out;
    color: #fff;
    animation: upanddown 0.5s linear 1s infinite alternate;
  }
  @keyframes upanddown {
    from {
      transform: scale(1);
      bottom: 30px;
    }
    to {
      transform: scale(1.2);
      bottom: 25px;
    }
  }
}
.service-section {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #f6f6f6;
  text-align: center;
  .section-title {
    font-size: 40px;
    margin-bottom: 80px;
    font-weight: bold;
    color: #444;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      width: 40px;
      height: 4px;
      background-color: #444;
      bottom: -20px;
      left: 50%;
      margin-left: -20px;
    }
  }
  .service-box {
    background-color: #fff;
    padding: 30px 20px;
    margin-bottom: 25px;
    box-shadow: 0 0 20px #ddd;
    border-radius: 6px;
    transition: 0.2s ease-in-out;
    display: block;
    text-decoration: none;
    color: #777;
    i {
      margin-bottom: 20px;
    }
    h4 {
      font-size: 18px;
    }
    &:hover {
      background-color: $flatGreen;
      i,
      h4 {
        color: #fff !important;
      }
    }
  }
  .col-md-6 {
    &:first-child i {
      color: #1abc9c;
    }
    &:nth-child(2) i {
      color: #f39c12;
    }
    &:nth-child(3) i {
      color: #27ae60;
    }
    &:nth-child(4) i {
      color: #2980b9;
    }
    &:nth-child(5) i {
      color: #e74c3c;
    }
    &:nth-child(6) i {
      color: #9b59b6;
    }
    &:nth-child(7) i {
      color: #34495e;
    }
    &:nth-child(8) i {
      color: #7f8c8d;
    }
  }
}
.news-section {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #e4e4e4;
  .news-box {
    background-color: #fff;
    text-align: center;
    border-radius: 6px;
    overflow: hidden;
    position: relative;
    transition: 0.3s ease-in-out;
    margin: 0 10px;
    &:hover {
      .overlay {
        transform: translate(0, 0);
      }
    }
    img {
      max-width: 100%;
    }
    h4 {
      font-size: 15px;
      padding: 15px 0;
      color: $flatGreen;
      font-weight: bold;
      margin: 0;
    }
    p {
      padding: 10px;
      font-size: 14px;
      text-align: right;
    }
    .overlay {
      position: absolute;
      right: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($flatGreen, 0.8);
      transition: 0.3s ease-in-out;
      transform: translate(-100%, -100%);
      a {
        color: #fff;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-weight: bold;
        font-size: 18px;
        border: 3px solid #fff;
        padding: 10px 20px 14px;
        border-radius: 15px;
        &:hover {
          background-color: #fff;
          color: #000;
          text-decoration: none;
        }
      }
    }
  }
  .slick-dots {
    padding: 0;
    text-align: center;
    list-style: none;
    margin-top: 30px;
    margin-bottom: 0;
    li {
      display: inline-block;
      margin-left: 10px;
      &.slick-active {
        button {
          background-color: $flatGreen;
        }
      }
      button {
        font-size: 0;
        width: 40px;
        height: 8px;
        border: none;
        background-color: #bfbfbf;
      }
    }
  }
}
