.upper-bar {
  padding: 10px;
  background-color: $flatGreen;
  .useful-links {
    a {
      color: #fff;
      font-family: Tahoma, Arial;
      font-size: 13px;
      font-weight: bold;
      transition: 0.3s ease-in-out;
      &:hover {
        text-decoration: none;
        color: #eee;
      }
      &:not(:last-child):after {
        content: " | ";
        color: #fff;
        margin: 0 5px;
      }
    }
  }
  .the-time {
    color: #fff;
    font-family: Tahoma, Arial;
    font-size: 14px;
    font-weight: bold;
  }
}
.main-nav {
  padding: 20px 20px 20px;
  border-bottom: 1px solid #f4f4f4;
  background-color: #fff;
  box-shadow: 0px 2px 0px #eaeaea;
  .the-logo {
    img {
      margin: 18px 0 0;
      @media (max-width: 767px) {
        margin-bottom: 30px;
      }
    }
  }
  .the-content {
    .search-box {
      width: 305px;
      position: relative;
      border-bottom: 1px solid #ccc;
      @media (max-width: 767px) {
        float: none;
        margin: auto;
      }
      .fa-search {
        position: absolute;
        top: 13px;
        right: 14px;
        color: #999;
      }
      input {
        padding: 5px 34px 5px 10px;
        background-color: #f7f7f7;
        width: 100%;
        border: 1px solid #ebebeb;
        height: 40px;
        font-family: Tahoma, Arial;
        &:focus {
          outline: none;
        }
      }
    }
    .icons-box {
      width: 305px;
      margin-top: 10px;
      overflow: hidden;
      @media (max-width: 767px) {
        float: none;
        margin: 10px auto;
      }
      a {
        text-align: center;
        background-color: #eee;
        width: 35px;
        height: 35px;
        line-height: 35px;
        float: right;
        color: #666;
        transition: 0.3s ease-in-out;
        &:hover {
          color: #fff;
        }
        &:not(:last-child) {
          margin-left: 10px;
        }
        &.facebook:hover {
          background-color: #3b5998;
        }
        &.twitter:hover {
          background-color: #1da1f2;
        }
        &.youtube:hover {
          background-color: #ff0000;
        }
        &.rss:hover {
          background-color: #f26522;
        }
        &.sitemap:hover {
          background-color: #008374;
        }
        &.android:hover {
          background-color: #a4c639;
        }
        &.phone:hover {
          background-color: #f6b128;
        }
      }
    }
  }
}
.lower-nav {
  background-color: #f8f8f8;
  .navbar-nav {
    padding: 0;
    text-align: right;
  }
  .dropdown-item {
    text-align: right;
  }
  @media (min-width: 992px) {
    .dropdown-menu {
      right: 0;
      left: auto;
      text-align: right;
      padding: 0;
      top: 120%;
      border: none;
      background-color: $flatGreen;
      min-width: 260px;
      &:before {
        content: "";
        border-style: solid;
        border-width: 10px;
        border-color: transparent transparent $flatGreen transparent;
        position: absolute;
        right: 20px;
        top: -20px;
      }
      .dropdown-item {
        padding: 10px 20px;
        color: #fff;
        transition: 0.3s ease-in-out;
        &:focus,
        &:hover {
          background: none;
          padding-right: 25px;
        }
      }
    }
  }
  .nav-link {
    font-size: 14px;
    color: #000 !important;
  }
  .dropdown-toggle::after {
    position: relative;
    top: 2px;
    right: 4px;
  }
  &.scrolling {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    background-color: rgba(58, 58, 58, 0.63);
    .nav-link {
      color: #fff !important;
    }
  }
}
