.landing-one {
  height: calc(100vh - 224px);
  position: relative;
  overflow: hidden;
  video {
    min-width: 100%;
    min-height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
  }
  .overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: rgba(black, 0.6);
  }
  .event-info {
    position: absolute;
    top: 50%;
    left: 50%;
    color: #fff;
    z-index: 3;
    width: 100%;
    text-align: center;
    transform: translate(-50%, -50%);
    .timer-elements {
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
      .the-time {
        font-size: 70px;
        flex: 1;
        max-width: 130px;
        span:last-child {
          display: block;
          font-size: 14px;
        }
        @media (max-width: 767px) {
          font-size: 20px;
          max-width: 60px;
        }
      }
      .separator {
        font-size: 70px;
        margin: 0 20px;
        @media (max-width: 767px) {
          font-size: 20px;
          margin: 0 10px;
        }
      }
    }
    .event-title {
      h2 {
        font-weight: bold;
        font-size: 48px;
        margin: 5px 0 30px;
        @media (max-width: 767px) {
          font-size: 35px;
          letter-spacing: 0;
          margin: 5px 0;
        }
      }
    }
    .event-description {
      font-weight: bold;
      font-size: 28px;
      @media (max-width: 767px) {
        font-size: 16px;
      }
    }
    .event-register {
      button {
        font-weight: bold;
        border: 2px solid #fff;
        color: #fff;
        background: transparent;
        padding: 10px 30px 14px;
        transition: all 0.3s ease-in-out;
        border-radius: 15px;
        font-size: 18px;
        margin-top: 20px;
        &:hover {
          background-color: #fff;
          color: $flatBlue;
        }
        &:focus {
          outline: none;
        }
      }
    }
  }
}
