.footer {
  background-color: #333;
  .content {
    padding: 20px;
    .links-list {
      margin: 20px 0;
      a {
        color: #eee;
        text-decoration: none;
        padding: 15px;
        display: block;
        transition: 0.3s ease-in-out;
        &:hover {
          background-color: #555;
        }
      }
      li {
        &:not(:last-child) {
          border-bottom: 1px solid #555;
        }
      }
    }
    .contacts-list {
      color: #eee;
      margin: 20px 0;
      li {
        padding: 15px;
        i {
          color: #eee;
          margin-left: 10px;
        }
      }
    }
    .ministry-logo {
      img {
        max-width: 100%;
        margin: 40px 0 0;
      }
    }
  }
  .copyright {
    background-color: #1a1a1a;
    padding: 20px;
    color: #bbb;
    font-size: 18px;
  }
}
